import React, { FC } from "react";
import { useSelector} from '../../services/hooks';
import orderInfo from '../../images/icon-info.svg';
import { leftFillNum } from '../../services/functions';


const OrderFailed: FC = () => {
  const order = useSelector(state => state.order);
  return (
    <>
      <h1 className="text text_type_digits-large mt-30">{leftFillNum(0, 7)}</h1>
      <p className="text text_type_main-medium mt-8">
        -   
      </p>
      <p className="text text_type_main-medium mt-8">
          {order.error}
      </p>
          
      <img className="mt-15" src={orderInfo} alt="  " />
      <p className="text text_type_main-default mt-15">     </p>
      <p className="text text_type_main-default text_color_inactive mt-2 mb-30">      </p>
    </>
  );
}


export default OrderFailed;
