import React, { FC } from 'react';
import { NavLink } from 'react-router-dom';
import styles from './ProfileNav.module.css';

const ProfileNav: FC = () => {
  return (    
        <nav className={styles.nav__list}>
         
            <NavLink to="/profile" exact className={`${styles.menu_link} text text_type_main-medium `} activeClassName={styles.menu_link_active}></NavLink>
            <NavLink to="/profile/orders" className={`${styles.menu_link} text text_type_main-medium `} activeClassName={styles.menu_link_active}> </NavLink>
            <NavLink to="/profile/logout" className={`${styles.menu_link} text text_type_main-medium `} activeClassName={styles.menu_link_active}></NavLink>
         
        </nav>   
  );  
}

export default ProfileNav;
